import LogicElementUtil from "../../contents/create/factory/util/logicElementUtil";
import LogicUtil from "../../contents/create/factory/util/logicUtil";
import { ScenarioData } from "../../contents/create/launcher/scenarioTab";
import ScenarioUtil from "../../contents/create/launcher/util/scenarioUtil";
import StoreCloud from "./storeCloud";
import StoreSystem from "./storeSystem";

/**
 * システムで使用するステート群
 */
export type Store = {
    system: StoreSystem.Param;
    libraryData: LogicUtil.WrapElement | null;
    scenarioData: ScenarioData | null;
    scenarioCacheList: ScenarioUtil.Cache[];
    cloud: StoreCloud.Props | null;
    fileHandle: FileSystemFileHandle | null;
    isPreviewMode: boolean;
}


export const ProjectTypes = ['library', 'scenario'] as const;
export type ProjectType = typeof ProjectTypes[number];

export const initialStore: Store = {
    system: {
        isSplash: true,
        user: null,
        dialog: null,
    },
    libraryData: null,
    scenarioData: null,
    scenarioCacheList: [],
    cloud: null,
    fileHandle: null,
    isPreviewMode: false,
}