import DataUtil from "../../utils/dataUtil";
import PreviewLogicFrame from "../create/preview/previewLogicFrame";
import { useContext, useEffect, useMemo, useState, useRef } from "react";
import LogicUtil from "../create/factory/util/logicUtil";
import LogicElementUtil from "../create/factory/util/logicElementUtil";
import styled from "styled-components";
import ScenarioUtil from "../create/launcher/util/scenarioUtil";
import { ScenarioData } from "../create/launcher/scenarioTab";
import FormUtil from "../../utils/form/formUtiil";
import PreviewUtil from "../create/preview/previewUtil";
import CanvasUtil from "../../utils/canvasUtil";
import PreviewLauncherFrame from "../create/preview/previewLauncherFrame";

type LibProps = {
    name: string;
    ownerId: string;
    lastmod: string;
    rootData: LogicUtil.WrapElement;
    seedList: SeedProps[];
}
type ScenarioProps = {
    name: string;
    ownerId: string;
    lastmod: string;
    libraryData: LogicUtil.WrapElement;
    scenarioData: ScenarioData;
}
type SeedProps = {
    name: string;
    disp?: string;
    argCount: number;
    master: LogicUtil.WrapElement;
}
type ScenarioPreviewProps = {
    sceens: string[];
    useFrame: number;
}

const DirectLinkManager = (props: {
    projectSeq: number;
}) => {
    const [libData, setLibData] = useState<LibProps | null>(null);
    const [scenarioData, setScenarioData] = useState<ScenarioProps | null>(null);
    const [scenarioPreviewProps, setScenarioPreviewProps] = useState<ScenarioPreviewProps | null>(null);

    const [seedSeq, setSeedSeq] = useState(-1);

    useEffect(() => {

        findProject(props.projectSeq).then(resList => {
            if (resList.length > 0) {
                const res = resList[0];
                const name = res.name;
                const ownerId = res.owner;
                const lastmod = res.lastmod;
                const data = JSON.parse(DataUtil.unZip(res.source));
                // シナリオであるかどうかの判定
                if (!data.hasOwnProperty('scenarioData')) {
                    const rootData = data as LogicUtil.WrapElement;
                    const seedMng = (rootData.data as LogicUtil.NodeRoot).seedMng.data as LogicUtil.NodeSeedManager;
                    const seedList: SeedProps[] = seedMng.defs
                        .filter(wrap => {
                            const seed = wrap.data as LogicUtil.NodeSeedItem;
                            return seed.permit === 'release';
                        })
                        .map(wrap => {
                            const seed = wrap.data as LogicUtil.NodeSeedItem;
                            const argMng = LogicElementUtil.getInnerWrapFixed(wrap, 'interface-mng', 'seedarg-mng');
                            const argCount = (argMng.data as LogicUtil.NodeSeedArgManager).args.length;
                            return { name: seed.name, disp: seed.disp, argCount, master: wrap };
                        });
                    setLibData({
                        name, ownerId, lastmod, rootData, seedList
                    });
                } else {
                    const scenarioProps = data as ScenarioUtil.ScenarioFileProps;
                    setScenarioData({
                        name, ownerId, lastmod, libraryData: scenarioProps.libraryData, scenarioData: scenarioProps.scenarioData
                    });
                }
            }
        });
    }, []);

    /**
     * プレビューオールダイアログを表示する
     */
    const openPreviewAllDialog = () => {
        if (scenarioData == null) return;
        const fuseList = scenarioData.scenarioData.fuseList;
        const limitFrame = scenarioData.scenarioData.limitFrame;
        const limitParticle = scenarioData.scenarioData.limitParticle;
        const minFrame = fuseList[0].frame;
        const logicData = scenarioData.libraryData;
        const sceens: string[] = [];
        let useFrame: number = 0;
        PreviewUtil.runParticleLogic({
            limitFrame,
            limitParticle,
            guaranteeFrame: minFrame,
            doIterate: (frame, owner) => {

                fuseList.some(def => {
                    if (def.frame === frame) {
                        LogicElementUtil.doParticleJob(logicData, def, owner);
                    } else if (def.frame > frame) return 1;
                });

                sceens.push(CanvasUtil.createURL(550, 550, (ctx) => {
                    // パーティクルの描画
                    owner.forEach(par => {
                        par.draw(ctx);
                    });
                    ctx.save();
                }));
            },
            doEnd: (frame) => {
                useFrame = frame;
            }
        });
        setScenarioPreviewProps({
            sceens, useFrame
        });
    }

    const getJsx = () => {
        let jsx = <></>;
        if (libData != null) {
            if (seedSeq === -1) {
                jsx = <>
                    <_Header>{libData.name}</_Header>
                    {libData.seedList.map((seed, i) => {
                        const seedName = seed.disp ?? seed.name;
                        return (<_Item key={i}
                            onClick={() => {
                                setSeedSeq(i);
                            }}
                        >
                            <_SeedName>{seedName}</_SeedName>
                            <_ArgCnt> [{seed.argCount} arguments]</_ArgCnt>
                        </_Item>);
                    })}
                </>;
            } else {
                jsx = <PreviewLogicFrame.Component
                    master={libData.seedList[seedSeq].master}
                    rootData={libData.rootData}
                    closeButtonAction={() => {
                        setSeedSeq(-1);
                    }}
                />
            }
        } else if (scenarioData != null) {
            if (scenarioPreviewProps == null) {
                jsx = <>
                    <_Header>{scenarioData.name}</_Header>
                    <FormUtil.ButtonRecord align="center" buttons={[
                        {
                            label: 'Build',
                            callback() {
                                openPreviewAllDialog();
                            },
                        }
                    ]} />
                </>;
            } else {
                jsx = <PreviewLauncherFrame.Component
                    sceens={scenarioPreviewProps.sceens}
                    frameCnt={scenarioPreviewProps.useFrame}
                />
            }
        }
        return jsx;
    }

    return getJsx();
}

export default DirectLinkManager;


const findProject = async (conteseq: number) => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `
        SELECT name, (select id from user_tbl where seq = prj.owner) as owner, source, lastmod
        FROM prjcttbl prj
        WHERE seq = ${conteseq}
    `);
    const results = await response.json();
    return results as any[];
};

const _Header = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: 50px;
    background: linear-gradient(to bottom, #00215e, #000000, #00215e);

    color: #e0e0e0;
    font-size: 28px;
    font-wieght: 600;
    line-height: 48px;
    padding-left: 8px;
    box-sizing: border-box;
`;

const _Item = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: 40px;
    background: linear-gradient(to bottom, #192f7e, #000540, #192f7e);

    font-size: 24px;
    font-wieght: 600;
    line-height: 38px;
    padding-left: 8px;
    box-sizing: border-box;
`;

const _SeedName = styled.span<{
}>`
    color: #abc9e6;
`;

const _ArgCnt = styled.span<{
}>`
    color: #fbffbf;
`;